@use 'base/mixins' as m;
@use 'base/variables' as v;

.categorias {
    background-color: v.$grisClaro;
    padding: 5rem 0;

    &__heading {
        text-align: center;
        color: v.$gris;
    }

    &__grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;

        @include m.tablet {
            grid-template-columns: repeat(3, 1fr);
        }
    }
}

.categoria {

    &__imagen {
        width: 100%;
        object-fit: cover;
    }
    &__heading {
        text-align: center;
        font-size: 2rem;
        margin: 0;
        padding: 2rem;
    }
}