@use 'base/mixins' as m;
@use 'base/variables' as v;

.header {

    &__barra {
        padding: 5rem 0 0 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        
        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
        }
    }
}

.hero {
    background-image: url(../img/hero.jpg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    margin-top: 5rem;
    @include m.tablet {
        margin: 0;
    }

    &__contenido {
        color: v.$blanco;
        max-width: 90rem;
        text-align: center;
        padding: 10rem;
        @include m.tablet {
            padding: 15rem;
        }
    }
    &__heading {
        font-size: 3.8rem;
    }
    &__enlace {
        @include m.boton(v.$terciario, v.$blanco)
    }
} 