@use 'base/mixins' as m;
@use 'base/variables' as v;

.eventos {
    padding: 5rem 0;

    &__heading {
        color: v.$gris;
        text-align: center;
    }

    &__grid {
        display: grid;
        gap: 3rem;

        @include m.tablet {
            grid-template-columns: repeat(3, 1fr);
        }
    }
}

.evento {
    border: 1px solid v.$grisClaro;

    &__imagen {
        width: 100%;
        object-fit: cover;
    }

    &__contenido {
        padding: 2rem;
    }
    &__fecha {
        font-size: 2rem;
        margin: 0;
        @include m.tablet {
            font-size: 1.4rem;
        }
    }
    &__heading {
        font-size: 4rem;
        margin: 1rem 0;
        @include m.tablet {
            font-size: 3rem;
        }
    }
    &__perfil {
        display: flex;
        gap: 2rem;
        align-items: center;
    }
    &__imagenAutor {
        border-radius: 100%;
        max-width: 12rem;
        max-height: 12rem;
        @include m.tablet {
            max-width: 7rem;
            max-height: 7rem;
        }
    }
    &__texto {
        font-weight: 700;
        font-size: 2.4rem;
        @include m.tablet {
            font-size: 1.4rem;
        }
    }
    &__autor {
        font-size: 2.4rem;
        @include m.tablet {
            font-size: 1.4rem;
        }
    }
}