@use 'base/mixins' as m;
@use 'base/variables' as v;

.grupos {
    padding: 4rem 0;
    background-image: linear-gradient(to bottom, v.$primario 0%, v.$primario 50%, v.$secundario 50%, v.$secundario 100%);

    @include m.tablet {
        background-image: linear-gradient(to right, v.$primario 0%, v.$primario 50%, v.$secundario 50%, v.$secundario 100%);
    }

    &__grid {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        gap: 3rem;

        @include m.tablet {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}

.grupo {
    color: v.$blanco;

    &__texto {
        color: v.$blanco;
        font-size: 2.2rem;
    }
}