@use 'base/mixins' as m;
@use 'base/variables' as v;

.navegacion {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;

    @include m.tablet {
        flex-direction: row;
    }

    &__enlace {
        font-weight: 700;

        &:first-child {
            color: v.$primario;

            @include m.tablet {
                border-right: 2px solid v.$grisClaro;
                padding: .8rem 2rem .8rem 0;
            }
        }
    }
}